
    .section-Apuesta-Vivo{
        color: var(--color-text-white);
        &__banner{
            height: auto;
        }
        &__description{
            border-radius: .5rem;
            border: 1px solid #0076ba;
            width: 60%;
            margin: 2rem auto;
            padding: 3rem 2rem;
            position: relative;
            top: -5rem;
        }
        &__button{
            display: flex;
            justify-content: center;
            margin: 2rem 0;
        }
        &__title{
           font-weight: bold;
        }
        &__text{
            padding: 1rem 1rem;
            color: var(--color-text-white);
        }
    }