.container-registro {
  width: 100%;
  height: 800px;
}

.container-registro__iframe {
  border: none;
  width: 100%;
  height: 800px;
}

:root {
  --header-height: 5rem;
  --color-border1: #ff796f;
  --color-gradient1: linear-gradient(0deg,#e67f77 0%,rgb(197 14 0) 46%,rgb(243 119 110) 53%,rgb(243 76 64) 54%,rgb(247 102 92) 76%,rgb(251 160 153) 100%);
  --color-gradient2: linear-gradient(0deg,#ffffff 0%,#bbbbbb 46%,#e4e4e4 53%,#dbdbdb 54%,#ffffff 76%,#FFF 100%);
  --color-blue: #3f51b5;
  --color-black: #000;
  --color-white: #fff;
  --color-orange: #ff9c00;
  --color-dark: #0a0a0a;
  --color-text-white: #FFFFFF;
  --color-gray: #1a1a1a;
  --color-gray-lith: #757474;
  --color-body-bg: #000;
  --alternate-font: sans-serif;
  --font-family-main: Noto Sans;
  --font-size: 16px;
  --line-height: 1.6;
}

@media screen and (min-width: 960px) {
  :root {
    --header-height: 7rem;
  }
}

.horses {
  color: var(--color-text-white);
}

.horses__banner {
  height: auto;
}

@media (max-width: 1400px) {
  .horses__banner {
    margin-top: 3rem;
  }
}

.horses__description {
  border-radius: .5rem;
  border: 1px solid #0076ba;
  width: 60%;
  margin: 2rem auto;
  padding: 3rem 2rem;
  position: relative;
  top: -5rem;
}

.horses__button {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}

.horses__title {
  font-weight: bold;
}

.horses__text {
  padding: 1rem 1rem;
  color: var(--color-text-white);
}

.centrar {
  text-align: center;
}

.caballos {
  max-width: 100%;
}

.caballos h2 {
  margin: 0;
}

@media (min-width: 474px) {
  .caballos {
    margin-top: 0rem;
  }
}

.caballos__content {
  width: 80%;
  max-width: 1200px;
  margin: 2rem;
}

.caballos__gallerie {
  margin-top: 2rem;
}

.caballos__button {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}

img {
  border: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}

:not(pre) code {
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  background: var(--color-light);
  color: var(--third-color);
  padding: 0 .25em;
  margin: 0 .1em;
  word-break: break-all;
}

.main-footer ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: var(--link-color);
  transition: all .3s ease-out;
}

a:hover {
  color: var(--link-hover-color);
}

::-webkit-scrollbar {
  display: none;
}

*:focus {
  outline: none;
}

.no-padding {
  padding-left: 0;
  padding-right: 0;
}
